.menu {
  display: flex;
  margin-bottom: 40px;
}

.main {
  max-height: 716px;
  overflow: auto;
  padding: 0 8px 0 12px;
}

.main::-webkit-scrollbar {
  width: 8px;
}

.main::-webkit-scrollbar-track {
  background: #2f2f37;
}

.main::-webkit-scrollbar-thumb {
  background-color: #8585ad;
}

.items {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 32px;
}

.item {
  display: flex;
  position: relative;
  flex-direction: column;
  row-gap: 9px;
  text-decoration: none;
  color: #fff;
  cursor: pointer;
  transition: 0.2s linear;
}

.isDragging {
  display: flex;
  position: relative;
  flex-direction: column;
  row-gap: 9px;
  text-decoration: none;
  cursor: pointer;
  color: #fff;
  transition: 0.2s linear;
  box-shadow: inset 0px 0px 10px 5px rgba(255, 0, 0, 1), 0px 0px 10px 5px rgba(255, 0, 0, 1);
  border: 3px solid red;
}

.item:hover {
  opacity: 0.6;
}

.counter {
  position: absolute;
  top: 0;
  right: 0;
}

.image {
  width: fit-content;
  margin: 0 auto;
}

.price {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 9px;
}

.title {
  display: flex;
  width: 272px;
  justify-content: center;
  text-align: center;
}
